<template>
  <div id="app">
    <main>
      <transition name="fade">
        <router-view/>
      </transition>
    </main>
    <div class="nav" @wheel="onWheel">
      <div class="vertical-line"></div>
      <router-link v-for="{title, url} in links" active-class="nav--item-selected" class="nav--item" :to="url">{{ title }}</router-link>
      <div class="vertical-line"></div>
    </div>
  </div>
</template>
<script>
  export default {
    data () {
      return {
        links: [
          {
            url: '/',
            title: 'home'
          },
          {
            url: '/news',
            title: 'news'
          },
          {
            url: '/about',
            title: 'about'
          }
        ],
        scroll: 0
      }
    },
    methods: {
      onWheel: function (event) {
        if (this.scroll < 0) {
          this.scroll = 0
          return
        }
        if (this.scroll > (this.links.length-1)*200) {
          this.scroll = (this.links.length-1)*200
          return
        }
        if (this.scroll % 200 === 0) {
          this.$router.push(this.links[this.scroll / 200].url)
        }
        this.scroll += event.deltaY
      }
    }
  }
</script>
